import React, {Component} from 'react';
import {render} from 'react-dom';

import ReactBotUI from '../../src';
import './demo.css';

class Demo extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div>
        <ReactBotUI
          dialogflow={{accessToken: '4804a95256c44e4f977e9824af5eb909'}}
          dialogHeightMax={350}
          isUserHidden={true}
          isVisible={true}
          title={'KatBot'}
          ref={el => this.chat = el} />
      </div>
    );
  }
}

render(<Demo/>, document.querySelector('#demo'))



